import React from "react";
import CustomMapProps from "./CustomMapProps";
import CustomMapState from "./CustomMapState";
import axios from 'axios' ;
import store from "../../redux/store/store";
import GoogleMap from "./GoogleMap";

export default class CustomMap extends React.Component<CustomMapProps, CustomMapState>{

    constructor(props : any){
        super(props);
        this.state = {
            naturalEventData : []
        }
    }

    componentDidMount(){
        this.fetchData() ;
    }

    async fetchData(){
        store.dispatch({
            type : "updateLoader",
            payload : {
                boolean : true
            }
        })
        await axios.get("https://eonet.sci.gsfc.nasa.gov/api/v2.1/events")
            .then( res =>{
                var data = res.data ;
                this.setState({naturalEventData : data.events}) ;
                store.dispatch({
                    type : "updateLoader",
                    payload : {
                        boolean : false
                    }
                })
            })
    }

    render(){
        return(
            <React.Fragment>
                <GoogleMap naturalEventData = {this.state.naturalEventData} /> 
            </React.Fragment>
        )
    }

}