import React, { Component } from 'react';
import GoogleMapReact from 'google-map-react';
import {AiFillFire} from 'react-icons/ai' 
 
const AnyReactComponent = ({ text }) => <div style={{ color : 'red', fontSize :'2rem' }} >{text}</div>;
 
export default class GoogleMap extends Component {

  static defaultProps = {
    center: {
      lat: 59.95,
      lng: 30.33
    },
    zoom: 11
  };

  constructor(props){
    super(props) ;
    this.state = {
      naturalEventData : [] 
    }

  }

  componentWillReceiveProps( props){
      this.setState({naturalEventData : props.naturalEventData}) ;
  }
 
  render() {
    var naturalEventData = this.state.naturalEventData ;
    var locMarker = [] ;
    locMarker = naturalEventData.map(
      rec => {
        if(rec.categories[0].id == "8" ){
          return (
              <AnyReactComponent
              lat={rec.geometries[0].coordinates[1]}
              lng={rec.geometries[0].coordinates[0]}
              text={ <AiFillFire />}
            />
          ) ;
        }
        return null ;
      }
    )
    return (
      <div style={{ height: '100vh', width: '100%' }}>
      <GoogleMapReact
        bootstrapURLKeys={{ key: "AIzaSyAq6157Lup2pN7zW4ukwvAkxu-PjTd8udA" }}
        defaultCenter={this.props.center}
        defaultZoom={this.props.zoom}
      >
         {locMarker}
      </GoogleMapReact>
    </div>
    );
  }
}
 