import React from "react";
import MaterialTable from 'material-table';

import TableProps from "./TableProps";
import TableState from "./TableState";
import axios from 'axios';
import { Line } from 'react-chartjs-2'
import CovidMS from "../CoronaVirus/CovidMS";
import { Dropdown } from 'primereact/dropdown';
import { InputNumber } from 'primereact/inputnumber';
import store from "../../redux/store/store";


export default class CustomTable extends React.Component<TableProps, TableState>{

    month = ['Jan', 'Feb', 'Mar', 'Apr', 'May', 'June', 'July', 'Aug', 'Sep', 'Oct', 'Nov', 'Dec']

    constructor(props: any) {
        super(props);
        this.state = {
            tableData: [],
            graphData: { timeline: { cases: [] }, cases: [] },
            globalData: {},
            country: { name: "Global", code: "Global" },
            span: 30
        }
        this.fetchGraphData.bind(this);
        this.fetchGlobalData.bind(this);
        this.scrollToGraph.bind(this);
        this.handelSpan.bind(this);
        this.handleCountry.bind(this);
        this.changeSpan.bind(this);
        this.fetchGraphData(CovidMS.globalGraphData + "?lastdays=30");
        this.fetchGlobalData();
    }

    componentWillReceiveProps(nextProps: any) {
        this.setState({ tableData: nextProps.tableData });
    }

    handelSpan(event: any) {
        this.setState({ span: event.value })
    }

    changeSpan() {
        if (this.state.country.name == "Global") {
            this.fetchGraphData(CovidMS.globalGraphData + "?lastdays=" + this.state.span);
        } else {
            this.fetchGraphData(CovidMS.graphData + this.state.country.name + "?lastdays=" + this.state.span);
        }
    }

    handleCountry(event: any) {
        this.setState({ country: event.target.value });
        if (event.target.value.name == "Global") {
            this.fetchGraphData(CovidMS.globalGraphData + "?lastdays=" + this.state.span);
        } else {
            this.fetchGraphData(CovidMS.graphData + event.target.value.name + "?lastdays=" + this.state.span);
        }
    }

    componentDidMount() {

    }

    fetchGraphData(url: string) {
        store.dispatch({
            type : "updateLoader",
            payload : {
                boolean : true
            }
        })
        axios.get(url)
            .then(res => {
                var data = res.data;
                this.setState({
                    graphData: data
                })
                store.dispatch({
                    type : "updateLoader",
                    payload : {
                        boolean : false
                    }
                })
            })
            .catch(error => {
                var obj = {
                    timeline : {
                        cases : [],
                        deaths : [],
                        recovered : []
                    }
                }
                this.setState({graphData : obj}) ;
                store.dispatch({
                    type : "updateLoader",
                    payload : {
                        boolean : false
                    }
                })
            })
    }

    fetchGlobalData() {
        store.dispatch({
            type : "updateLoader",
            payload : {
                boolean : true
            }
        })
        axios.get(CovidMS.globalCovidData)
            .then(res => {
                var data = res.data;
                this.setState({
                    globalData: data
                })
                store.dispatch({
                    type : "updateLoader",
                    payload : {
                        boolean : false
                    }
                })
            })
    }


    scrollToGraph(state: any) {
        var elem: any = document.getElementById("graphID");
        var obj = { target: { value: { name: state, code: state } } };
        if (state != "Global") {
            this.handleCountry(obj);
        }
        elem.scrollIntoView();
    }

    render() {
        var cases: any = {};
        var death: any = {};
        var recovered: any = {};
        if (this.state.country.name == "Global") {
            cases = this.state.graphData.cases;
            death = this.state.graphData.deaths;
            recovered = this.state.graphData.recovered;
        } else {
            if (this.state.graphData.timeline != undefined) {
                cases = this.state.graphData.timeline.cases;
                death = this.state.graphData.timeline.deaths;
                recovered = this.state.graphData.timeline.recovered;
            }
        }

        var label = Object.keys(cases);
        var chartLabel = [];
        var confirmed = [];
        var active = [];
        var deathList = [];
        var recoveredList = [];
        for (var i = 0; i < label.length; i++) {
            var date = new Date(label[i]);
            var formattedDate = date.getDate() + "-" + this.month[date.getMonth()];
            chartLabel.push(formattedDate);
            var currentConfirmed = cases[label[i]];
            var currentDeath = death[label[i]];
            var currentRecovered = recovered[label[i]];

            confirmed.push(currentConfirmed);
            active.push(currentConfirmed - currentDeath - currentRecovered);
            deathList.push(currentDeath);
            recoveredList.push(currentRecovered);
        }

        var sortType: any = "desc";
        const data = this.state.tableData;
        var arrayOfCountries = [];
        arrayOfCountries.push({ name: "Global", code: "Global" });
        for (var k = 0; k < this.state.tableData.length; k++) {
            var obj = { "name": this.state.tableData[k].country, "code": this.state.tableData[k].country };
            arrayOfCountries.push(obj);
        }
        const detailedPanel = [];
        const col = [{
            title: "Country", field: "country",
            cellStyle: {
                color: "blue",
                textDecoration: "underline"
            },
            render: (rowData: any) => (<span className="cursorPointer" onClick={() => this.scrollToGraph(rowData.country)}  ><img src={rowData.countryInfo.flag} style={{ width: '30%' }} alt="Flag not available" /><span className="ml-1">{rowData.country}</span></span>),
            headerStyle: {
                backgroundColor: '#039be5'
            }
        },
        {
            title: "Population", field: "population",
            headerStyle: {
                backgroundColor: '#039be5'
            }
        },
        {
            title: "Tested", field: "tests",
            headerStyle: {
                backgroundColor: '#039be5'
            }
        },
        {
            title: "Infected", field: "cases",
            defaultSort: sortType,
            headerStyle: {
                backgroundColor: '#039be5'
            },
        },
        {
            title: "Active", field: "active",
            headerStyle: {
                backgroundColor: '#039be5'
            },
        }, {
            title: "Recovered", field: "recovered",
            headerStyle: {
                backgroundColor: '#039be5'
            }
        },
        {
            title: "Deceased", field: "deaths",
            headerStyle: {
                backgroundColor: '#039be5'
            }
        },
        {
            title: "Critical", field: "critical",
            headerStyle: {
                backgroundColor: '#039be5'
            }
        }
        ]
        return (
            <React.Fragment>
                <div className="w-75 marginLeft_12 text-white">
                    <h2 className="text-left titlePage">COVID-19 Coronavirus Pandemic</h2>
                    <p className="card-text text-left"><small className="text-muted">Coronavirus disease 2019 (COVID-19) is defined as illness caused by a novel coronavirus now called severe acute respiratory syndrome coronavirus 2 (SARS-CoV-2; formerly called 2019-nCoV), which was first identified amid an outbreak of respiratory illness cases in Wuhan City, Hubei Province, China.</small></p>
                    <div className="card border-primary mb-3 centre" style={{ maxWidth: "18rem" }} >
                        <div className="card-header text-dark"><h4>Coronavirus Cases</h4></div>
                        <div className="card-body text-primary">
                            <div className="w-100 text-left text-danger">Confirmed : {this.state.globalData.cases}<small>+{this.state.globalData.todayCases}</small></div>
                            <div className="w-100 text-left text-primary">Active : {this.state.globalData.active}</div>
                            <div className="w-100 text-left text-success">Recovered : {this.state.globalData.recovered}<small>+{this.state.globalData.todayRecovered}</small></div>
                            <div className="w-100 text-left text-secondary">Death : {this.state.globalData.deaths}<small>+{this.state.globalData.todayDeaths}</small></div>
                            <a onClick={() => this.scrollToGraph("Global")} className="btn btn-outline-primary mt-3">Spread Trends</a>
                        </div>
                    </div>
                </div>
                <div className="w-75 marginLeft_12">
                    <MaterialTable
                        title="Covid-19 Latest Data"
                        data={data}
                        columns={col}
                    />
                </div>

                <div id="graphID" className="w-75 marginLeft_12 mt-5">
                    <h4 className="text-warning" >Spread Trends</h4>
                    <div className="row">
                        <div className="col-md-6 text-white p-2">
                            <Dropdown className="w-75" optionLabel="name" onChange={this.handleCountry.bind(this)} value={this.state.country} options={arrayOfCountries} placeholder="Select a City" />
                        </div>
                        <div className="col-md-6 text-white p-2">
                            <InputNumber id="in" value={this.state.span} onChange={this.handelSpan.bind(this)} />
                            <button className="btn btn-outline-primary ml-2" onClick={this.changeSpan.bind(this)} >Change Span</button>
                        </div>
                    </div>
                    <div className="w-100 mt-3" >
                        <Line
                            data={{
                                labels: chartLabel,
                                datasets: [{
                                    label: 'Confirmed',
                                    data: confirmed,
                                    borderColor: '#dc3545',
                                    backgroundColor: '#ff9090'
                                }]
                            }
                            }
                            height={250}
                            options={
                                {
                                    maintainAspectRatio: false,
                                    responsive: true,
                                    scales: {
                                        xAxes: [{
                                            gridLines: {
                                                color: "rgba(0, 0, 0, 0)",
                                            },
                                            ticks: {
                                                fontColor: "#CCC", // this here
                                            },
                                        }],
                                        yAxes: [{
                                            gridLines: {
                                                color: "rgba(0, 0, 0, 0)",
                                            },
                                            ticks: {
                                                fontColor: "#CCC", // this here
                                            }
                                        }],
                                    }
                                }
                            }
                        />
                    </div>
                    <div className="w-100 mt-3" >
                        <Line
                            data={{
                                labels: chartLabel,
                                datasets: [{
                                    label: 'Active',
                                    data: active,
                                    borderColor: '#007bff',
                                    backgroundColor: '#7b7bff'
                                }]
                            }
                            }
                            height={250}
                            options={
                                {
                                    maintainAspectRatio: false,
                                    responsive: true,
                                    scales: {
                                        xAxes: [{
                                            gridLines: {
                                                color: "rgba(0, 0, 0, 0)",
                                            },
                                            ticks: {
                                                fontColor: "#CCC", // this here
                                            },
                                        }],
                                        yAxes: [{
                                            gridLines: {
                                                color: "rgba(0, 0, 0, 0)",
                                            },
                                            ticks: {
                                                fontColor: "#CCC", // this here
                                            }
                                        }],
                                    }
                                }
                            }
                        />
                    </div>
                    <div className="w-100 mt-3" >
                        <Line
                            data={{
                                labels: chartLabel,
                                datasets: [{
                                    label: 'Recovered',
                                    data: recoveredList,
                                    borderColor: '#28a745',
                                    backgroundColor: '#88ff88'
                                }]
                            }
                            }
                            height={250}
                            options={
                                {
                                    maintainAspectRatio: false,
                                    responsive: true,
                                    scales: {
                                        xAxes: [{
                                            gridLines: {
                                                color: "rgba(0, 0, 0, 0)",
                                            },
                                            ticks: {
                                                fontColor: "#CCC", // this here
                                            },
                                        }],
                                        yAxes: [{
                                            gridLines: {
                                                color: "rgba(0, 0, 0, 0)",
                                            },
                                            ticks: {
                                                fontColor: "#CCC", // this here
                                            }
                                        }],
                                    }
                                }
                            }
                        />
                    </div>
                    <div className="w-100 mt-3" >
                        <Line
                            data={{
                                labels: chartLabel,
                                datasets: [{
                                    label: 'Death',
                                    data: deathList,
                                    borderColor: '#6c757d',
                                    backgroundColor: '#c3c3c3'
                                }],
                            }
                            }
                            height={250}
                            options={
                                {
                                    maintainAspectRatio: false,
                                    responsive: true,
                                    scales: {
                                        xAxes: [{
                                            gridLines: {
                                                color: "rgba(0, 0, 0, 0)",
                                            },
                                            ticks: {
                                                fontColor: "#CCC", // this here
                                            },
                                        }],
                                        yAxes: [{
                                            gridLines: {
                                                color: "rgba(0, 0, 0, 0)",
                                            },
                                            ticks: {
                                                fontColor: "#CCC", // this here
                                            }
                                        }],
                                    }
                                }
                            }
                        />
                    </div>
                    <div className="w-100 pt-5">

                    </div>
                </div>

            </React.Fragment>
        )
    }

} 